.carousel-item {
  background-size: cover;
  height: 780px;
  width: 100%;
}
.carousel-item .carousel-content {
  width: 80%;
  padding-top: 80px;
  margin-left: auto;
  margin-right: auto;
}
.carousel-item .carousel-content .inner {
  width: 60%;
}
.carousel-item .carousel-content .inner h2 {
  font-size: 4.5rem;
  line-height: 1;
  --tw-text-opacity: 1;
  color: #65498a;
  font-family: "clan_procond_news_italic";
  margin-bottom: 0;
}
.carousel-item .carousel-content .inner h4 {
  font-size: 24px;
  font-weight: 700;
  line-height: 2rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
  font-family: Arial, sans-serif;
  color: #4b384c;
}
.carousel-item .carousel-content .inner p {
  font-size: 20px;
  line-height: 31px;
  margin-bottom: 2.5rem;
}
.carousel-item .carousel-content .inner .cta {
  position: relative;
  font-size: 20px;
  line-height: 31px;
  text-transform: uppercase;
  text-decoration: none;
  font-size: 20px;
  line-height: 31px;
  margin-top: 1rem;
  color: #65498a;
  border: 1px solid #65498a;
  padding: 15px;
  border-radius: 0px 0px 15px 0px;
  transition: background-color 0.3s, color 0.3s, transform 0.3s;
}
.carousel-item .carousel-content .inner .cta:hover {
  background-color: #65498a;
  color: #ffffff;
  padding: 20px;
  transform: scale(1.05); /* Increase size by 5% on hover */
}

#second-slider {
  margin-right: 80px;
  margin-left: 80px;
  margin-top: 80px;
  margin-bottom: 80px;
}
#second-slider .cols .inner {
  max-width: 350px;
  height: 550px;
  border-radius: 0px 0px 15px 0px;
}
#second-slider .cols .inner .thumbnails {
  height: 250px;
  width: 350px;
  object-fit: cover;
}
#second-slider .cols .inner h2 {
  font-size: 1.5rem;
  font-weight: 700;
}
#second-slider .cols .inner h4 {
  font-size: 1.2rem;
  margin: 0;
}
#second-slider .cols .inner p {
  font-size: 0.8rem;
  margin: 0;
  margin-top: 1rem;
  margin-bottom: 2rem;
}
#second-slider .cols .inner .cta {
